import React, { useContext, useState } from 'react';
import { BookContext } from '../context/BookContext';

const BookForm = () => {
  const { addBook } = useContext(BookContext);
  const [title, setTitle] = useState('');
  const [author, setAuthor] = useState('');
  const [tags, setTags] = useState([]);
  const [date, setDate] = useState('');
  const [sinopse, setSinopse] = useState('');
  const [poster, setPoster] = useState('');

  const handleSubmit = e => {
    e.preventDefault();
    addBook(title, author, tags, date, sinopse, poster);
    setTitle('');
    setAuthor('');
    setTags('');
    setDate('');
    setSinopse('');
    setPoster('');
  };
  return (
    <form className='form' onSubmit={handleSubmit}>
      <h1 className='form__title'>Add a Book</h1>
      <div className='field'>
        <label>Title</label>
        <input
          type='text'
          placeholder='Book Title'
          value={title}
          onChange={({ target }) => setTitle(target.value)}
          required
        />
      </div>

      <div className='field'>
        <label>Author</label>
        <input
          type='text'
          placeholder='Book Author'
          value={author}
          onChange={({ target }) => setAuthor(target.value)}
        />
      </div>

      <div className='field'>
        <label>Date</label>
        <input
          type='text'
          placeholder='Date'
          value={date}
          onChange={({ target }) => setDate(target.value)}
        />
      </div>

      <div className='field'>
        <label htmlFor='sinopse'>Sinopse</label>
        <textarea
          name='sinopse'
          rows='4'
          placeholder='Sinopse ...'
          value={sinopse}
          onChange={({ target }) => setSinopse(target.value)}
        ></textarea>
      </div>

      <div className='field'>
        <label>Tags</label>
        <input
          type='text'
          placeholder='Tags separated by comma'
          value={tags}
          onChange={({ target }) => setTags(target.value.split(','))}
        />
      </div>

      <div className='field'>
        <label>Poster</label>
        <input
          type='text'
          placeholder='Link'
          value={poster}
          onChange={({ target }) => setPoster(target.value)}
        />
      </div>

      <input className='btn primary' type='submit' value='Add Book' />
    </form>
  );
};

export default BookForm;
